<template>
  <div>
    <div>
      <div>{{ p_title }} - 病例列表</div>
      <div class="d-inline">百分比</div>
      <b-button variant="outline-dark" v-for="sym in symptom_list"
                :key="'percent'+sym.conid" size="sm" class="mx-1"
                :to="{name:'symptom_related_disease',params:{ati_title: sym.ati_title, ati_id: sym.ati_id}}">
        {{ sym.ati_title }}
        <b-badge variant="light">{{ Math.round(sym.a_score_per * 100) }}%</b-badge>
      </b-button>
    </div>
    <div>
      <div>{{ p_title }} - 特点</div>
      <b-list-group>
        <b-list-group-item v-for="item in characteristic" :key="item">{{ item }}</b-list-group-item>
      </b-list-group>
    </div>
  </div>
</template>

<script>
export default {
  name: "disease-related-symptom",
  props: ['p_id', 'p_title'],
  data() {
    return {
      symptom_list: [],
      characteristic: [],
      links: []
    }
  },
  mounted() {
    this.axios.get('/api/case_dbase/symptom_temp_list/' + this.p_id).then(res => {
      this.symptom_list = res.data
    }).catch((err) => console.error('symptom_temp_list error', err))

    this.axios.get('/api/case_dbase/' + this.p_id).then(res => {
      this.characteristic = res.data.p_tedian.split('\n')
      this.links = res.data.p_link.split('\n')
    }).catch((err) => console.error('error', err))
  }
}
</script>

