import caselist from './components/management/case-dbase'
import symptom_sort from './components/management/symptom-sort'
import symptom_management from './components/management/symptom-management'
import disease_management from './components/management/disease-management'
import dbase_edit from './components/management/subcomponents/dbase-edit'
import symptom_related_disease from "./components/management/subcomponents/symptom-related-disease";
import disease_related_symptom from "./components/management/subcomponents/disease-related-symptom"
import disease_edit from './components/management/subcomponents/disease-edit'

const routes = [
    {path: '/case_dbase', component: caselist, name: 'case_dbase'},
    {path: '/symptom_sort', component: symptom_sort, name: 'symptom_sort'},
    {path: '/symptom_management', component: symptom_management, name: 'symptom_management'},
    {path: '/disease_management', component: disease_management, name: 'disease_management'},
    {path: '/case_dbase/:id', component: dbase_edit, props: true, name: 'dbase_edit'},
    {
        path: '/case_dbase/related_disease/:ati_id',
        component: symptom_related_disease,
        props: true,
        name: 'symptom_related_disease'
    },
    {
        path: '/case_dbase/related_symptom/:p_id',
        component: disease_related_symptom,
        props: true,
        name: 'disease_related_symptom'
    },
    {
        path:'/disease_management/:p_id',
        component: disease_edit,
        props: true,
        name:'disease_edit'
    }
]

export default routes