<template>
  <b-modal title="编辑症状信息" id="symptom_add_edit" cancel-title="取消" ok-title="确定" @ok="handleOk" @show="reset">
    <b-overlay :show="loading" rounded="sm">
      <b-form ref="symptom_form" @submit.stop.prevent="handleSubmit" :validated="form_validated">
        <b-form-group label="分类:" label-for="sort">
          <b-form-select v-model="form.att_id" :options="attr_options" required></b-form-select>
        </b-form-group>
        <b-form-group label="名称:" label-for="name_input">
          <b-form-input id="name_input" v-model="form.ati_title" required></b-form-input>
        </b-form-group>
        <b-form-group label="同义词:" label-for="content_input">
          <b-form-textarea id="content_input" rows="3" max-rows="8" v-model="form.ati_content"></b-form-textarea>
        </b-form-group>
      </b-form>
    </b-overlay>
  </b-modal>
</template>

<script>
export default {
  name: "symptom_add_edit",
  props: ['ati_id', 'mode'],
  data() {
    return {
      form: {
        att_id: 0,
        ati_title: '',
        ati_content: ''
      },

      attr_options: [],
      form_validated: null,
      loading: true
    }
  },
  methods: {
    reset() {
      this.loading = true
      this.attr_options = []
      // 先请求一下分类列表
      this.axios.get('/api/symptom_sort').then(response => {
        response.data.data.forEach((item) => {
          this.attr_options.push({value: item.att_id, text: item.att_name})
        })
      }).then(() => {
        if (this.mode === 'update') {
          return this.axios.get('/api/symptom_management/' + this.ati_id)
              .then(response => {
                this.form = response.data
                this.form_validated = null
              })
        } else {
          this.form = {
            att_id: 0,
            ati_title: '',
            ati_content: ''
          }
          this.form_validated = null
        }
      })
          .catch(err => {
            this.$message.error('错误')
            console.error(err)
          }).finally(() => this.loading = false)
    },
    handleOk(bvModalEvt) {
      // Prevent modal from closing
      bvModalEvt.preventDefault()
      // Trigger submit handler
      this.handleSubmit()
    },
    successful() {
      this.$message({
        message: '成功',
        type: 'success'
      });
    },
    error() {
      this.$message({
        message: '失败',
        type: 'error'
      });
    },
    networkSubmitForm() {
      if (this.mode === "submit") {
        return this.axios.post("/api/symptom_management/new", this.form)
            .then(() => this.successful()).catch(() => this.error())
      } else if (this.mode === "update") {
        return this.axios.post("/api/symptom_management/update/" + this.ati_id, this.form)
            .then(() => this.successful()).catch(() => this.error())
      }
    },
    handleSubmit() {
      // Exit when the form isn't valid
      if (!this.$refs.symptom_form.checkValidity()) {
        this.form_validated = true
        return
      }
      // Push the name to submitted names
      this.networkSubmitForm().then(() => {
        this.$emit('refresh_table')
        this.$nextTick(() => this.$bvModal.hide('symptom_add_edit'))
      })
      // Hide the modal manually
    }
  }
}
</script>

