<template>
  <b-modal title="编辑新类型" id="case_sort_add_edit" cancel-title="取消" ok-title="确定" @ok="handleOk" @show="reset">
    <b-form ref="case_sort_form" @submit.stop.prevent="handleSubmit" :validated="form_validated">
      <b-form-group label="名称:" label-for="name_input">
        <b-form-input id="name_input" v-model="form.att_name" required></b-form-input>
      </b-form-group>
      <b-form-group label="是否显示:" label-for="visible_radio">
        <b-form-radio-group id="visible_radio" v-model="form.att_status" name="visible_radio" required>
          <b-form-radio :value="1">显示</b-form-radio>
          <b-form-radio :value="0">不显示</b-form-radio>
        </b-form-radio-group>
      </b-form-group>
      <b-form-group label="排序:" label-for="rank_input">
        <b-form-input id="rank_input" v-model="form.att_sort" type="number" required></b-form-input>
      </b-form-group>
    </b-form>
  </b-modal>
</template>

<script>
export default {
  name: "symptom-sort-add-edit",
  props: ["name", "visible", "rank", "mode", "update_id"],
  data() {
    return {
      form: {
        att_name: "",
        att_status: "",
        att_sort: ""
      },
      form_validated: null
    }
  },
  methods: {
    reset() {
      this.form = {att_name: this.name, att_status: this.visible, att_sort: this.rank};
      this.form_validated = null
    },
    handleOk(bvModalEvt) {
      // Prevent modal from closing
      bvModalEvt.preventDefault()
      // Trigger submit handler
      this.handleSubmit()
    },
    successful() {
      this.$message({
        message: '成功',
        type: 'success'
      });
    },
    error() {
      this.$message({
        message: '失败',
        type: 'error'
      });
    },
    networkSubmitForm() {
      if (this.mode === "submit") {
        return this.axios.post('/api/symptom_sort/new', this.form).then(() => this.successful()).catch(() => this.error())
      } else if (this.mode === "update") {
        return this.axios.post('/api/symptom_sort/update/' + this.update_id, this.form).then(() => this.successful()).catch(() => this.error())
      }
    },
    handleSubmit() {
      // Exit when the form isn't valid
      if (!this.$refs.case_sort_form.checkValidity()) {
        this.form_validated = true
        return
      }
      // Push the name to submitted names
      this.networkSubmitForm().then(() => {
        this.$emit('refresh_table')
        this.$nextTick(() => this.$bvModal.hide('case_sort_add_edit'))
      })
      // Hide the modal manually
    }
  }
}
</script>

