<template>
  <div>
    <b-card bg-variant="light" header="管理系统" class="login-card need-center">
      <form>
        <h3>登录</h3>
        <div class="form-group">
          <label>用户名</label>
          <input class="form-control form-control-lg" v-model="username"/>
        </div>
        <div class="form-group">
          <label>密码</label>
          <input type="password" class="form-control form-control-lg" v-model="password"/>
        </div>
        <button type="submit" class="btn btn-dark btn-lg btn-block" @click="check_login">登录</button>

      </form>
    </b-card>


  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    check_login() {
      if (this.username === 'admin' && this.password === '123456') {
        this.$message.success('登陆成功')
        this.$emit('login', this.username)
      } else {
        this.$message.error('用户名或密码错误')
      }
    }
  }
}
</script>

<style scoped>
.login-card {
  width: fit-content;
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translateX(50%) translateY(-50%);
}
</style>