<template>
  <div>
    <management v-if="isLogin" :username="login_username"/>
    <login @login="login" v-else></login>
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
// import Appointment_table from "@/components/appointment";
import management from "@/components/management";
import login from "@/components/login";

export default {
  name: 'App',
  components: {
    management,
    login
  },
  data() {
    return {
      isLogin: false,
      login_username: ''
    }
  },
  methods: {
    login(username) {
      this.isLogin = true
      this.login_username = username
      this.$cookies.set('JSESSIONID', 'AE2EC34DFB==')
      this.$nextTick(() => {
        this.$router.push({name: 'case_dbase'})
      })
    }
  },
  mounted() {
    if (this.$cookies.get('JSESSIONID') === 'AE2EC34DFB==') {
      this.login('admin')
    }
  }
}
</script>

