<template>
  <b-modal title="编辑新类型" id="disease_add_edit" cancel-title="取消" ok-title="确定" @ok="handleOk" @show="reset">
    <b-form ref="disease_form" @submit.stop.prevent="handleSubmit" :validated="form_validated">
      <b-form-group label="名称:" label-for="name_input">
        <b-form-input id="name_input" v-model="form.p_title" required></b-form-input>
      </b-form-group>
      <b-form-group label="排序:" label-for="rank_input">
        <b-form-input id="rank_input" v-model="form.p_sort" type="number" required></b-form-input>
      </b-form-group>
    </b-form>
  </b-modal>
</template>

<script>
export default {
  name: "disease-add-edit",
  props: ["p_title", "p_sort", "mode", "p_id"],
  data() {
    return {
      form: {
        p_title: "",
        p_sort: ""
      },
      form_validated: null
    }
  },
  methods: {
    reset() {
      this.form = {
        p_title: this.p_title,
        p_sort: this.p_sort
      };
      this.form_validated = null
    },
    handleOk(bvModalEvt) {
      // Prevent modal from closing
      bvModalEvt.preventDefault()
      // Trigger submit handler
      this.handleSubmit()
    },
    successful() {
      this.$message({
        message: '成功',
        type: 'success'
      });
    },
    error() {
      this.$message({
        message: '失败',
        type: 'error'
      });
    },
    networkSubmitForm() {
      if (this.mode === "submit") {
        return this.axios.post('/api/disease_management/new', this.form).then(() => this.successful()).catch(() => this.error())
      } else if (this.mode === "update") {
        return this.axios.post('/api/disease_management/update/' + this.p_id, this.form).then(() => this.successful()).catch(() => this.error())
      }
    },
    handleSubmit() {
      // Exit when the form isn't valid
      if (!this.$refs.disease_form.checkValidity()) {
        this.form_validated = true
        return
      }
      // Push the name to submitted names
      this.networkSubmitForm().then(() => {
        this.$emit('refresh_table')
        this.$nextTick(() => this.$bvModal.hide('disease_add_edit'))
      })
      // Hide the modal manually
    }
  }
}
</script>

