<template>
  <div>
    <symptom_add_edit :ati_id="ati_id" :mode="mode" @refresh_table="refresh_table"></symptom_add_edit>
    <b-button variant="primary" @click="createItem()"><i class="fa fa-plus"></i>添加</b-button>
    <vuetable ref="vuetable7"
              api-url="/api/symptom_management"
              :fields="symptom_management_fields"
              :first-page="0"
              :css="css.table"
              :per-page="20"
              @vuetable:pagination-data="onPaginationData"
    >
      <div slot="operation-slot" slot-scope="props">
        <b-button size="sm" variant="info" class="mx-1" @click="updateItem(props.rowData)"><i class='fa fa-edit'></i>修改
        </b-button>
        <b-button size="sm" variant="danger" class="mx-1" @click="deleteItem(props.rowData.ati_id)"><i class='fa fa-trash'></i>删除
        </b-button>
      </div>
    </vuetable>
    <div class="pagination ui basic segment grid">
      <vuetable-pagination ref="pagination7" :css="css.pagination"
                           @vuetable-pagination:change-page="onChangePage"
      ></vuetable-pagination>
    </div>
  </div>
</template>

<script>
import symptom_management_fields from "@/components/management/fields/symptom_management_fields";
import Vuetable from 'vuetable-2'
import VuetablePagination from "vuetable-2/src/components/VuetablePagination";
import default_style from '@/styles/appointment_table_style'
import symptom_add_edit from './subcomponents/add-edit-model/symptom-add-edit'

export default {
  name: "symptom_management",
  components: {
    Vuetable,
    VuetablePagination,
    symptom_add_edit
  },
  data() {
    return {
      symptom_management_fields,
      css: default_style,

      ati_id: 0,
      mode: 'update'
    }
  },
  methods: {
    onPaginationData(paginationData) {
      this.$refs.pagination7.setPaginationData(paginationData);
    },
    onChangePage(page) {
      this.$refs.vuetable7.changePage(page);
    },
    refresh_table() {
      this.$refs.vuetable7.reload()
    },
    createItem() {
      this.ati_id = 0
      this.mode = "submit"
      this.$nextTick(() => this.$bvModal.show('symptom_add_edit'))
    },
    updateItem(item_data) {
      this.ati_id = item_data.ati_id
      this.mode = "update"
      this.$nextTick(() => this.$bvModal.show('symptom_add_edit'))
    },
    deleteItem(ati_id) {
      this.$alert('是否删除？', "提示", {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(() => this.axios.post("/api/symptom_management/delete", {ati_id}).catch(() => this.$message(
          {message: '失败', type: 'error'})))
          .then(() => this.$message(
              {message: '成功', type: 'success'})).finally(this.refresh_table)
    }
  }
}
</script>

<style>
/*@import "../../styles/pagination.css";*/
.font-small {
  font-size: small;
}
</style>