<template>
  <div>
    <div>{{ ati_title }}的关联症状</div>
    <vuetable ref="vuetable6"
              :api-url="api_url"
              :fields="symptom_related_disease_fields"
              :css="css.table"
              @vuetable:row-clicked="handleRowClick"
    ></vuetable>
  </div>
</template>

<script>
// 传入一个症状，返回与其关联的疾病及每个疾病中该症状的得分。
import symptom_related_disease_fields from "@/components/management/fields/symptom_related_disease_fields";
import Vuetable from 'vuetable-2'
import default_style from '@/styles/appointment_table_style'

export default {
  name: "symptom-related-disease",
  components: {
    Vuetable
  },
  props: ['ati_title', 'ati_id'],
  data() {
    return {
      symptom_related_disease_fields,
      css: default_style,
      api_url: '/api/case_dbase/symptom_related_disease/' + this.ati_id
    }
  },
  methods: {
    handleRowClick(data) {
      this.$router.push({
        name: 'disease_related_symptom',
        params: {p_id: data.data.p_id, p_title: data.data.p_title}
      })
    }
  }
}
</script>

