<template>
  <div>
    <symptom_sort_add_edit
        :name="name" :visible="visible" :rank="rank" :mode="mode" :update_id="update_id" @refresh_table="refresh_table"
    ></symptom_sort_add_edit>
    <b-button variant="primary" @click="createItem()"><i class="fa fa-plus"></i>添加</b-button>
    <vuetable ref="vuetable2"
              api-url="/api/symptom_sort"
              :fields="symptom_sort_fields"
              :first-page="0"
              :css="css.table"
              :per-page="20"
    >
      <div slot="operation-slot" slot-scope="props">
        <b-button size="sm" variant="info" class="mx-2" @click="updateItem(props.rowData)"><i class='fa fa-edit'></i>修改
        </b-button>
        <b-button size="sm" variant="danger" class="mx-2" @click="deleteItem(props.rowData.att_id)"><i class='fa fa-trash'></i>删除</b-button>
      </div>
    </vuetable>
  </div>
</template>

<script>
import symptom_sort_fields from "@/components/management/fields/symptom_sort_fields";
import Vuetable from 'vuetable-2'
import default_style from '@/styles/appointment_table_style'
import symptom_sort_add_edit from "@/components/management/subcomponents/add-edit-model/case-sort-add-edit";

export default {
  name: "case-sort",
  components: {
    symptom_sort_add_edit,
    Vuetable},
  data() {
    return {
      symptom_sort_fields,
      css: default_style,
      name: "", visible: null, rank: "", mode: "submit", update_id: ""// mode: submit update
    }
  },
  methods: {
    refresh_table() {
      this.$refs.vuetable2.reload()
    },
    // onPaginationData(paginationData) {
    //   this.$refs.pagination2.setPaginationData(paginationData);
    // },
    // onChangePage(page) {
    //   this.$refs.vuetable2.changePage(page);
    // },
    createItem() {
      this.name = ""
      this.visible = null
      this.rank = ""
      this.mode = "submit"
      this.$nextTick(() => this.$bvModal.show('case_sort_add_edit'))
    },
    updateItem(item_data) {
      this.name = item_data.att_name
      this.visible = item_data.att_status
      this.rank = item_data.att_sort
      this.mode = "update"
      this.update_id = item_data.att_id
      this.$nextTick(() => this.$bvModal.show('case_sort_add_edit'))
    },
    deleteItem(att_id) {
      this.$alert('是否删除？', "提示", {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(() => this.axios.post('/api/symptom_sort/delete', {att_id})).then(() => this.$message(
          {message: '成功', type: 'success'})).catch(() => this.$message(
          {message: '失败', type: 'error'})).finally(this.refresh_table)
    }
  }
}
</script>

