<template>
  <div>
    <disease_add_edit :mode="this.mode" :p_id="p_id" :p_sort="p_sort" :p_title="p_title"
                      @refresh_table="refresh_table"></disease_add_edit>
    <b-button variant="primary" @click="createItem()"><i class="fa fa-plus"></i>添加</b-button>
    <vuetable ref="vuetable7"
              api-url="/api/disease_management"
              :fields="disease_management_fields"
              :first-page="0"
              :css="css.table"
              :per-page="20"
              @vuetable:pagination-data="onPaginationData"
    >
      <div slot="operation-slot" slot-scope="props">
        <b-button size="sm" variant="info" class="mx-1" @click="updateItem(props.rowData)"><i class='fa fa-edit'></i>修改
        </b-button>
        <b-button size="sm" variant="danger" class="mx-1" @click="deleteItem(props.rowData.p_id)"><i
            class='fa fa-trash'></i>删除
        </b-button>
      </div>

      <div slot="property-slot" slot-scope="props">
        <b-button size="sm" variant="light"
                  :to="{name:'disease_edit',params:{p_id:props.rowData.p_id,p_title:props.rowData.p_title}}"><i
            class='fa fa-info-circle'></i>属性
        </b-button>
      </div>
    </vuetable>
    <div class="pagination ui basic segment grid">
      <vuetable-pagination ref="pagination7" :css="css.pagination"
                           @vuetable-pagination:change-page="onChangePage"
      ></vuetable-pagination>
    </div>
  </div>
</template>

<script>
import disease_management_fields from "@/components/management/fields/disease_management_fields";
import Vuetable from 'vuetable-2'
import VuetablePagination from "vuetable-2/src/components/VuetablePagination";
import default_style from '@/styles/appointment_table_style'
import disease_add_edit from './subcomponents/add-edit-model/disease-add-edit'

export default {
  name: "disease_management",
  components: {
    Vuetable,
    VuetablePagination,
    disease_add_edit
  },
  data() {
    return {
      disease_management_fields,
      css: default_style,

      mode: 'submit',
      p_title: '',
      p_sort: '',
      p_id: ''
    }
  },
  methods: {
    onPaginationData(paginationData) {
      this.$refs.pagination7.setPaginationData(paginationData);
    },
    onChangePage(page) {
      this.$refs.vuetable7.changePage(page);
    },
    refresh_table() {
      this.$refs.vuetable7.reload()
    },
    createItem() {
      this.p_id = 0
      this.p_title = ''
      this.p_sort = ''
      this.mode = "submit"
      this.$nextTick(() => this.$bvModal.show('disease_add_edit'))
    },
    updateItem(item_data) {
      this.p_id = item_data.p_id
      this.p_title = item_data.p_title
      this.p_sort = item_data.p_sort
      this.mode = "update"
      this.$nextTick(() => this.$bvModal.show('disease_add_edit'))
    },
    deleteItem(p_id) {
      this.$alert('是否删除？', "提示", {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(() => this.axios.post("/api/disease_management/delete", {ati_id: p_id}).catch(() => this.$message(
          {message: '失败', type: 'error'})))
          .then(() => this.$message(
              {message: '成功', type: 'success'})).finally(this.refresh_table)
    }
  }
}
</script>

<style scoped>

</style>