<template>
  <div>
    <div>特点</div>
    <b-form-textarea v-model="p_tedian" debounce="500" rows="3" max-rows="5"></b-form-textarea>
    <div>检查</div>
    <b-form-textarea v-model="p_jiancha" debounce="500" rows="3" max-rows="5"></b-form-textarea>
    <div>治疗</div>
    <b-form-textarea v-model="p_zhiliao" debounce="500" rows="3" max-rows="5"></b-form-textarea>
    <div>链接</div>
    <b-form-textarea v-model="p_link" debounce="500" rows="3" max-rows="5"></b-form-textarea>
    <b-button variant="primary" @click="submit">提交</b-button>
  </div>

</template>

<script>
// 这个页面是由路由直接跳转过来
export default {
  name: "disease",
  props: ['p_title', 'p_id'],
  data() {
    return {
      p_tedian: '',
      p_jiancha: '',
      p_zhiliao: '',
      p_link: ''
    }
  },
  mounted() {
    this.axios.get('/api/disease_management/' + this.p_id).then(response => {
      this.p_tedian = response.data.p_tedian
      this.p_jiancha = response.data.p_jiancha
      this.p_zhiliao = response.data.p_zhiliao
      this.p_link = response.data.p_link
    })
  },
  methods: {
    successful() {
      this.$message({
        message: '成功',
        type: 'success'
      });
    },
    error() {
      this.$message({
        message: '失败',
        type: 'error'
      });
    },
    submit() {
      this.axios.post('/api/disease_management/update/' + this.p_id, {
        p_tedian: this.p_tedian,
        p_jiancha: this.p_jiancha,
        p_zhiliao: this.p_zhiliao,
        p_link: this.p_link
      }).then(() => {
        this.successful()
        this.$router.push({name:'disease_management'})
      }).catch(this.error)
    }
  }
}
</script>

