<template>
  <div>
    <div>病例列表，按照疾病进行分类</div>
    <vuetable ref="vuetable"
              api-url="/api/case_dbase"
              :fields="case_dbase_fields"
              :first-page="0"
              :css="css.table"
              :per-page="20"
              @vuetable:pagination-data="onPaginationData"
    >
      <div slot="operation-slot" slot-scope="props">
        <b-button size="sm" variant="info" :to="{name:'dbase_edit', params:{id:props.rowData.p_id, name:props.rowData.p_title}}"><i class='fa fa-edit'></i>修改</b-button>
      </div>
    </vuetable>
    <div class="pagination ui basic segment grid">
      <vuetable-pagination ref="pagination" :css="css.pagination"
                           @vuetable-pagination:change-page="onChangePage"
      ></vuetable-pagination>
    </div>
  </div>
</template>

<script>
import case_dbase_fields from "@/components/management/fields/case_dbase_fields";
import Vuetable from 'vuetable-2'
import VuetablePagination from "vuetable-2/src/components/VuetablePagination";
import default_style from '@/styles/appointment_table_style'

export default {
  name: "case_dbase",
  components: {
    Vuetable,
    VuetablePagination
  },
  data() {
    return {
      case_dbase_fields,
      css: default_style
    }
  },
  methods: {
    onPaginationData(paginationData) {
      this.$refs.pagination.setPaginationData(paginationData);
    },
    onChangePage(page) {
      this.$refs.vuetable.changePage(page);
    }
  }
}
</script>

<style>
@import "../../styles/pagination.css";
</style>