<template>
  <b-modal title="编辑患者信息" id="dbase_add_edit" cancel-title="取消" ok-title="确定" @ok="handleOk" @show="reset">
    <b-overlay :show="loading" rounded="sm">
      <b-form ref="dbase_form" @submit.stop.prevent="handleSubmit" :validated="form_validated">
        <b-form-group label="年龄:" label-for="age_input">
          <b-form-input id="age_input" v-model="form.cas_age" type="number" required></b-form-input>
        </b-form-group>
        <b-form-group label="性别:" label-for="gender_input">
          <b-form-radio-group id="gender_input" v-model="form.cas_sex" name="gender_radio" required>
            <b-form-radio :value="1">男</b-form-radio>
            <b-form-radio :value="0">女</b-form-radio>
          </b-form-radio-group>
        </b-form-group>
        <b-form-group label="病例描述" label-for="case_description">
          <b-form-textarea id="case_description" rows="3" max-rows="8" v-model="form.cas_content" required></b-form-textarea>
        </b-form-group>
        <b-form-group label="阳性症状" label-for="positive_symptom">
          <b-form-textarea id="positive_symptom" rows="3" max-rows="8" v-model="form.cas_po" required></b-form-textarea>
        </b-form-group>
        <b-form-group label="阴性症状" label-for="negative_symptom">
          <b-form-input id="negative_symptom" rows="1" max-rows="8" v-model="form.cas_ne" :maxlength="10" required></b-form-input>
        </b-form-group>
        <b-form-group label="链接" label-for="link">
          <b-form-textarea id="link" rows="3" max-rows="8" v-model="form.cas_link" required></b-form-textarea>
        </b-form-group>
      </b-form>
    </b-overlay>
  </b-modal>
</template>

<script>
export default {
  name: "dbase_add_edit",
  props: ['cas_id', 'mode', 'cas_pid'],
  data() {
    return {
      form: {
        cas_age: 0,
        cas_sex: null,
        cas_content: "",
        cas_po: "",
        cas_ne: "",
        cas_link: ""
      },
      form_validated: null,
      loading: false
    }
  },
  methods: {
    reset() {
      if (this.mode === 'update') {
        this.loading = true
        this.axios.get('/api/case_dbase/case_temp_row/' + this.cas_id).then(response => {
          this.form = response.data
          this.form_validated = null
        }).catch(err => {
          this.$message.error('错误')
          console.error(err)
        }).finally(() => this.loading = false)
      } else {
        this.form = {
          cas_age: 0,
          cas_sex: 0,
          cas_content: "",
          cas_po: "",
          cas_ne: "",
          cas_link: ""
        }
        this.form_validated = null
      }
    },
    handleOk(bvModalEvt) {
      // Prevent modal from closing
      bvModalEvt.preventDefault()
      // Trigger submit handler
      this.handleSubmit()
    },
    successful() {
      this.$message({
        message: '成功',
        type: 'success'
      });
    },
    error() {
      this.$message({
        message: '失败',
        type: 'error'
      });
    },
    networkSubmitForm() {
      if (this.mode === "submit") {
        // 如果创建新的记录，则别忘了需要一个cas_pid的值，其实下面的更新api也是为了配合他。
        return this.axios.post(`/api/case_dbase/case_temp_list/${this.cas_pid}/new`, this.form)
            .then(() => this.successful()).catch(() => this.error())
      } else if (this.mode === "update") {
        return this.axios.post(`/api/case_dbase/case_temp_list/${this.cas_pid}/update/${this.cas_id}`, this.form)
            .then(() => this.successful()).catch(() => this.error())
      }
    },
    handleSubmit() {
      // Exit when the form isn't valid
      if (!this.$refs.dbase_form.checkValidity()) {
        this.form_validated = true
        return
      }
      // Push the name to submitted names
      this.networkSubmitForm().then(() => {
        this.$emit('refresh_table')
        this.$nextTick(() => this.$bvModal.hide('dbase_add_edit'))
      })
      // Hide the modal manually
    }
  }
}
</script>

