<template>
  <div>
    <dbase_add_edit :cas_id="cas_id" :cas_pid="cas_pid" :mode="mode" @refresh_table="refresh_table"></dbase_add_edit>

    <div>
      <div>{{ name }} - 病例列表</div>
      <div class="d-inline">百分比</div>
      <b-button variant="outline-dark" v-for="sym in symptom_list"
                :key="'percent'+sym.conid" size="sm" class="mx-1"
                :to="{name:'symptom_related_disease',params:{ati_title: sym.ati_title, ati_id: sym.ati_id}}">
        {{ sym.ati_title }}
        <b-badge variant="light">{{ Math.round(sym.a_score_per * 100) }}%</b-badge>
      </b-button>
    </div>

    <div class="mt-2">
      <div class="d-inline">分数</div>
      <b-button variant="outline-dark" v-for="sym in symptom_list"
                :key="'score'+sym.conid" size="sm" class="mx-1"
                :to="{name:'symptom_related_disease',params:{ati_title: sym.ati_title, ati_id: sym.ati_id}}">
        {{ sym.ati_title }}
        <b-badge variant="light">{{ sym.a_score_new }}</b-badge>
      </b-button>
    </div>

    <div>病例列表</div>
    <b-button variant="primary" @click="createItem()"><i class="fa fa-plus"></i>添加</b-button>
    <vuetable ref="vuetable5"
              :api-url="'/api/case_dbase/case_temp_list/'+ id"
              :fields="sub_case_dbase_case_temp_list_fields"
              :first-page="0"
              :css="css.table"
              :per-page="20"
              @vuetable:pagination-data="onPaginationData">
      <div slot="operation-slot" slot-scope="props">
        <b-button size="sm" variant="info" class="mx-5" @click="updateItem(props.rowData)"><i class='fa fa-edit'></i>修改
        </b-button>
        <b-button size="sm" variant="danger" class="mx-5" @click="deleteItem(props.rowData.cas_id)"><i
            class='fa fa-trash'></i>删除
        </b-button>
      </div>
    </vuetable>
    <div class="pagination ui basic segment grid">
      <vuetable-pagination ref="pagination5" :css="css.pagination"
                           @vuetable-pagination:change-page="onChangePage"
      ></vuetable-pagination>
    </div>
  </div>

</template>

<script>
import sub_case_dbase_case_temp_list_fields from "@/components/management/fields/sub_case_dbase_case_temp_list_fields";
import Vuetable from 'vuetable-2'
import VuetablePagination from "vuetable-2/src/components/VuetablePagination";
import default_style from '@/styles/appointment_table_style'
import dbase_add_edit from './add-edit-model/dbase-add-edit'

export default {
  name: "dbase-edit",
  // 永远别忘记这个页面是哪里来的！这个页面是router直接渲染出来的！
  props: ['name', 'id'],
  data() {
    return {
      css: default_style,
      symptom_list: [{
        conid: 1,
        ati_id: 1,
        ati_title: "加载中...",
        a_score_new: 0,
        a_score_per: 0.0
      }],
      sub_case_dbase_case_temp_list_fields,

      cas_id: 0,
      cas_pid: 0,
      mode: 'submit'

    }
  },
  components: {
    Vuetable,
    VuetablePagination,
    dbase_add_edit
  },
  methods: {
    onPaginationData(paginationData) {
      this.$refs.pagination5.setPaginationData(paginationData);
    },
    onChangePage(page) {
      this.$refs.vuetable5.changePage(page);
    },
    refresh_table() {
      this.$refs.vuetable5.reload()
    },
    createItem() {
      this.cas_id = 0
      this.cas_pid = this.id
      this.mode = "submit"
      this.$nextTick(() => this.$bvModal.show('dbase_add_edit'))
    },
    updateItem(item_data) {
      this.cas_id = item_data.cas_id
      this.cas_pid = this.id
      this.mode = "update"
      this.$nextTick(() => this.$bvModal.show('dbase_add_edit'))
    },
    deleteItem(cas_id) {
      this.$alert('是否删除？', "提示", {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(() => this.axios.post(`/api/case_dbase/case_temp_list/${this.id}/delete`, {cas_id})).then(() => this.$message(
          {message: '成功', type: 'success'})).catch(() => this.$message(
          {message: '失败', type: 'error'})).finally(this.refresh_table)
    }
  },
  mounted() {
    this.axios.get('/api/case_dbase/symptom_temp_list/' + this.id).then(res => {
      this.symptom_list = res.data
    }).catch((err) => console.error('symptom_temp_list error', err))
    //     .finally(() =>
    //     console.log(this.symptom_list)
    // )
    // this.axios.get('/api/case_temp_list/' + this.id).then(res => {
    //   this.case_list = res.data
    // }).catch((err) => console.error('case_temp_list error', err))
  }
}
</script>

